.greybackground div ul li, a{
   
    background-color: rgba(121, 114, 114);
    color: rgb(93, 179, 182);
    width: 100%;
    margin: 0;
    padding: 5px;
    list-style: none;
   
}
span{
    position: absolute;
    left: 35%;
    color: cyan;
    background-color: darkcyan;
    border-radius: 18px;
    font-weight: 800;
    font-size: 3rem;
}
.greybackground{
    display: flex;
    margin: 0;
    padding: 0;
}
.greybackground div {
    width: 50%;
    background-color: rgba(121, 114, 114);
    margin: 0;
    padding: 0;
}
.greybackground div ul {
    margin: 0;
    padding: 10;
    text-align: center;
    background-color: rgba(121, 114, 114);
    border-radius: 8px;
}
