.banner1{
    margin: 0px;
    padding: 0px;
    background-color: coral;
    display: flex;
}
h1{
    margin: 0px;
    padding: 0px;
    color: aquamarine;
}
h2{
    margin: 0px;
    padding: 0px;
    color: aquamarine;
}
button{
    padding: 10px;
    color: brown;
    border-radius: 10px;
    background-color: yellowgreen;
    font-weight: bolder;
}
.banner1 div{
    padding: 50px 0 0   0;
    display: block;
    width: 50%;
text-align: center;
