}

.collor{
    padding: 0;
    margin: 0;
    background: linear-gradient(to left,rgba(236, 7, 7, 0.8),rgba(132, 250, 250, 0.5));
}
.color2{
    background: linear-gradient(to right,rgba(236, 7, 7, 0.8),rgba(132, 250, 250, 0.5));

}

.bannerImage{
    width: 80%;
    height: auto;
}
.bannerImage2{
    width: 150%;
}