.head{
    background: linear-gradient(to bottom right,aqua,white);
   
    border: 2px solid rgb(207, 21, 21);
    display: block;
    margin: 0px;
    padding: 0px;
    height: 30px;
}
.head ul li{
    
    list-style: none;
    padding: 0 0 0 10%;
}
.head ul{
    margin: 0px;
    padding: 0px;
    display: flex;
}
.head ul li a{
    
    text-decoration: none;
}
img{
    padding: 0;
    margin: 0;
    width: 30px;
    height: auto;
}